import React from 'react';

import Layout from '../components/Layout';

const IndexPage = () => (
  <Layout>
    <article id="main">
      <header>
        <h2>Projects</h2>
        <p>recent projects</p>
      </header>
      <section className="wrapper style5">
        <div className="inner">
          <section>
            <header>
              <h4>TaskBot</h4>
              <p>Telegram BOT</p>
            </header>
            <p>
              Developed a Telegram bot that automates the creation of a daily
              "to do" list. The bot, named "@JRM_TaskBOT", utilizes the
              JavaScript language, along with the Express.js web framework and
              Node.js runtime environment. To store and retrieve task records, I
              incorporated PostGreSQL and developed custom APIs for seamless
              interaction with the database. This project exemplified the
              effective utilization of technology to create a user-friendly and
              efficient task management solution through Telegram.
            </p>
            <p>
              <strong>Bot Link: </strong>
              <a href="https://t.me/JRM_TaskBOT">telegram taskbot</a>
              <br />
              <strong>Technology Stack:</strong> JavaScript, Express.js, Nodejs,
              PostGreSQL
            </p>
            <hr />
            <header>
              <h4>Python Binance Automation</h4>
              <p>Trading Automation</p>
            </header>
            <p>
              Developed an automation script designed to read data from a
              spreadsheet file and execute automated trades based on the
              specified details. The script facilitated seamless integration
              with the Binance platform, enabling efficient and accurate trade
              execution. The project, hosted on GitHub and showcased the
              utilization of Python to create a powerful and flexible trading
              automation solution.
            </p>
            <p>
              <strong>Github: </strong>
              <a href="https://github.com/SilverJRM/PyBOT_BinanceSignal">
                Python Trade Automation
              </a>
              <br />
              <strong>Technology Stack:</strong> Python
            </p>
            <hr />
            <header>
              <h4>Python File Automation</h4>
              <p>File Validation Automation</p>
            </header>
            <p>
              Implemented a batch process for automated file validation,
              specifically targeting Excel files stored in a SharePoint
              location. The process involved performing comprehensive validation
              checks on the files, generating a detailed report using MS Teams,
              and subsequently moving the files to a designated target location
              based on the validation results. The solution was built utilizing
              Python, Jenkins, and SharePoint, ensuring efficient and reliable
              automation of the file validation process.
            </p>
            <p>
              <strong>Technology Stack:</strong> Python, Jenkins, SharePoint
            </p>
            <hr />
            <header>
              <h4>File Upload Interface</h4>
              <p>Web-based React User Interface with Expanded Features</p>
            </header>
            <p>
              Revamp the existing user interface built with React by introducing
              advanced functionalities such as interactive charts, customizable
              widgets, and comprehensive reports.
            </p>
            <p>
              <strong>Technology Stack:</strong> JavaScript, React, Redux,
              Nodejs
            </p>
            <hr />
            <header>
              <h4>AUTOMATED TRADING BOT</h4>
              <p>Algorithmic Trading System Utilizing Chainlink and Binance</p>
            </header>
            <p>
              developed a trading algorithm that leveraged real-time price data
              from cryptocurrency assets via Chainlink, enabling informed
              decisions on trade initiation and closure on the Binance platform.
              The system was developed using JavaScript, Node.js, Express.js,
              and PostGreSQL for efficient tracking of asset prices and trades
              through API integration.
            </p>
            <p>
              <strong>Technology Stack:</strong> JavaScript, Nodejs, Express.js,
              PostGreSQL
            </p>
            <hr />
            <header>
              <h4>Smartcontract Lottery</h4>
              <p>Ethereum Smartcontract Lottery</p>
            </header>
            <p>
              Developed an Ethereum smart contract lottery, utilizing
              JavaScript, Solidity, Hardhat Framework, and CSS. The project
              involved creating a lottery system using a smart contract,
              enabling secure and transparent participation in the lottery.
            </p>
            <p>
              <strong>Technology Stack:</strong> JavaScript, Solidity, Hardhat
              Framework, CSS
            </p>
          </section>
        </div>
      </section>
    </article>
  </Layout>
);

export default IndexPage;
